.small-button {
  background-color: rgb(23, 136, 106);
  border: none;
  padding: 10px 30px;
  border-radius: 10px;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

.small-button-dark {
  background-color: rgb(22, 76, 150);
  border: none;
  padding: 10px 30px;
  border-radius: 10px;
  color: white;
  font-weight: bold;
  cursor: pointer;
}
